body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  text-align: center;
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100%;
  width: 100%;
}

.box {
  display: flex;
  flex-direction: column;
  height: 100%;
}

.header {
  flex: 0 1 auto;
}

.board {
  padding: .8em;
  display: grid;
  grid-template-columns: repeat(5, -webkit-max-content);
  grid-template-columns: repeat(5, max-content);
  grid-template-rows: repeat(5, auto);
  overflow-y: hidden;
  justify-content: center;
  align-items: center;
}

.list {
  margin: 1em;
  display: flex;
  flex-direction: column;
  flex: 1 1 20em;
  overflow-y: scroll;
}

.footer {
  flex: 0 1 30px;
  display: flex;
  flex-direction: row;
  align-items: center;
}

.footer>p {
  margin: 0 1em;
}

.btn {
  padding: .7em .5em;
  margin: .5em;
  border: none;
  border-radius: 5px;
  font-weight: 600;
  box-shadow: rgba(100, 100, 111, 0.2) 0px 7px 29px 0px;
}

.resetBtn {
  background-color: #AA2200;
  color: #FFF;
}

.loginBtn {
  background-color: #004499;
  color: #FFF;
}
.bingoItem {
  color: red;
  padding: 3px;
  font-size: 3em;
  max-width: 10rem;
  max-height: 5rem;
  border: 1.5px solid black;
}

.itemChecked {
  color: grey;
}
.checkItem {
  display: flex;
  flex-direction: row;
  align-items: center;
  background-color: #ededed;
  padding: 5px;
  margin: .4rem 0;
}

.numberId {
  margin: 0 3px;
  width: 1.5em;
}

.checkItem > p, strike {
  margin: 0 5px;
  text-align: start;
}
.userContainer {
  display: flex;
  flex-direction: column;
  justify-content: center;
  border: 1px solid #000;
  border-radius: 10px;
  padding: 2em;
  margin: 2em;
}

.continue {
  background-color: #004499;
  color: #fff;
  padding: .7em .5em;
  margin: .5em;
  margin-top: 2em;
  border: none;
  border-radius: 5px;
  font-weight: 600;
  box-shadow: rgba(100, 100, 111, 0.2) 0px 7px 29px 0px;
}

.continue:hover {
  background-color: #0055BB;
}

.title {
  font-size: 1.4em;
}

.selectBox {
  height: 40px;
  padding: .5em .7em;
  margin: .5em;
}
